import React, {useState, useEffect} from "react";
import PropTypes from "prop-types";
import keycode from "keycode";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableFooter from "@material-ui/core/TableFooter";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import PlaylistPlayIcon from "@material-ui/icons/PlaylistAdd";
import CardEditMenu from './CardEditMenu.js'
import SearchBox from '../../../../../components/SearchBox/index';
import {useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
import {base_url, $http} from "config";
import IntlMessages from 'util/IntlMessages';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import SweetAlert from 'react-bootstrap-sweetalert'

const columnData = [
  {id: "id", align: false, disablePadding: true, label: "ID"},
  {id: "name", align: true, disablePadding: false, label: "Company Name"},
  {id: "website", align: true, disablePadding: false, label: "Website"},
  {id: "user_count", align: true, disablePadding: false, label: "Users"},
  {id: "site_count", align: true, disablePadding: false, label: "Sites"},
  {id: "room_count", align: true, disablePadding: false, label: "Rooms"},
  {id: "project_count", align: true, disablePadding: false, label: "Projects"},
  {id: "status", align: true, disablePadding: false, label: "Status"},
  {id: "", align: true, disablePadding: false, numeric:'right', label: "Actions"}
];

const CustomerTableHead = (props) => {

  const createSortHandler = property => event => {
    props.onRequestSort(event, property);
  };

    const {order, orderBy} = props;

    return (
      <TableHead>
        <TableRow>
          {columnData.map(column => {
            return (
              <TableCell
                key={column.id}
                align={column.numeric}
              >
                <Tooltip
                  title="Sort"
                  placement={column.numeric ? "bottom-end" : "bottom-start"}
                  enterDelay={300}
                >
                    <TableSortLabel
                    active={orderBy === column.id}
                    direction={order}
                    onClick={createSortHandler(column.id)}
                  >
                    {column.label}
                  </TableSortLabel>
                </Tooltip>
              </TableCell>
            );
          })}
        </TableRow>
      </TableHead>
    );
};

CustomerTableHead.propTypes = {
  onRequestSort: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
  orderBy: PropTypes.string.isRequired
};


let CustomerTableToolbar = (props) => {
  const History = useHistory();

  const NewCustomer = () => {
    History.push('company/customer-edit');
  }

  const {SearchCustomer} = props;

  return (
    <Toolbar className="table-header">
      <div className="title">
        <Typography variant="h6">Customers</Typography>
      </div>
      <div className="col-md-3 col-lg-3 col-sx-6 col-6 ml-auto">
          <SearchBox placeholder="Search ..."
            onChange={(event) => SearchCustomer(event.target.value)}
          />
      </div>
      <div className="actions">
        <Tooltip title="New Customer">
          <IconButton aria-label="New Customer" onClick={()=>NewCustomer()}>
              <PlaylistPlayIcon/>
          </IconButton>
        </Tooltip>
      </div>
    </Toolbar>
  );
};

const CustomerTable = () => {
  
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("id");
  const [selected, setSelected] = useState([]);
  const [loader, setLoader] = useState(false);
  const {width} = useSelector(({settings}) => settings);
  const History = useHistory();

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [tabledata, setTabledata] = useState([]);
  const [Customers, setCustomers] = useState([]);
  const [warning, setWarning] = useState(false)

  useEffect(() => {
    init_data();
  }, []);

  const init_data = () => {
    $http.get(base_url + "customers/company")
    .then((response) => {
      setTabledata(response.data.customers);
      setCustomers(response.data.customers);
      setLoader(true);
    }).catch((error) => {
      setWarning(true)
    });
  }
  const handleRequestSort = (event, property) => {

    let sort_order;
    if (orderBy === property && order === "asc") {
      sort_order = "desc";
    } else {
      sort_order = "asc";
    }

    sort_order === "desc"
      ? Customers.sort((a, b) => (b[property] < a[property] ? -1 : 1))
      : Customers.sort((a, b) => (a[property] < b[property] ? -1 : 1));
    
    setOrder(sort_order);
    setOrderBy(property);
  };


  const handleClick = (id) => {
    History.push({
      pathname: "/app/customers/company/info",
      state: {id: id}
    })
  };

  const handleChangePage = (event, page) => {
    setPage(page);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(event.target.value);
  };
  const onSelectCustomer = (data, index) => {
    if (index === 0) {
      History.push({
        pathname: "/app/customers/company/info",
        state: {id: data}
      })
    } else if (index === 1) {
      History.push({
        pathname: "/app/customers/company/customer-edit",
        state: {id: data}
      })
    } else if (index === 2) {
      const frmData = new FormData();
      frmData.append('id', data);
      $http.post(base_url + "customers/DeleteCompanyCustomer", frmData)
      .then((response) => {
        if (response.data.status === "success") {
          if(navigator.onLine)
            init_data()
          
        } else if (response.data.status === "error") {
          NotificationManager.error(response.data.msg, "Error!", 1000, () => {
        });
      }
      }).catch((error)=>{
        NotificationManager.error(error, "Error!", 1000, () => {
        });
      })
      //modify get request
      if(!navigator.onLine){
        caches.open('SirvezApp').then(function(cache){
          cache.match('/api/customers/company')
          .then((response)=>response.json())
          .then(function(responseData){
            for (var i = 0; i < responseData.customers.length; i++) {
              if (responseData.customers[i].id == data) {
                responseData.customers.splice(i,1)
                const jsonResponse = new Response(JSON.stringify(responseData), {
                  headers: {
                      'content-type': 'application/json'
                  }
                });
                cache.put('/api/customers/company', jsonResponse.clone())
                .then(function(result){
                  init_data()
                })
                break;
              }
            }
          })
        })
      }
      
    }
  }

  const SearchCustomer = (data) => {
    setCustomers(
      tabledata.filter(function (item) {
        return (item.name.toLowerCase().includes(data.toLowerCase()));
      })
    );
  }

  return (
    
        <div>
              <CustomerTableToolbar SearchCustomer={SearchCustomer}/>
          {
            loader ? (
              <div className="flex-auto">
                <div className="table-responsive-material">
                  <Table className="">
                    <CustomerTableHead
                      order={order}
                      orderBy={orderBy}
                      onRequestSort={handleRequestSort}
                    />
                    <TableBody>
                      {
                        Customers.length > 0 ?
                        Customers.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((n) => 
                          <TableRow
                            hover
                            key={n.id}
                            // onClick={event => handleClick(event, n.id)}
                            //onKeyDown={event => handleKeyDown(event, n.id)}
                            tabIndex={-1}
                          >
                            <TableCell onClick={() => handleClick(n.id)}>{n.id}</TableCell>
                            <TableCell onClick={() => handleClick(n.id)}>{n.name}</TableCell>
                            <TableCell onClick={() => handleClick(n.id)}>{n.website}</TableCell>
                            <TableCell onClick={() => handleClick(n.id)}>{n.user_count}</TableCell>
                            <TableCell onClick={() => handleClick(n.id)}>{n.site_count}</TableCell>
                            <TableCell onClick={() => handleClick(n.id)}>{n.room_count}</TableCell>
                            <TableCell onClick={() => handleClick(n.id)}>{n.project_count}</TableCell>
                            <TableCell>
                                <div className={` badge text-uppercase bg-success`}>{n.status ? 'Active': 'Deactive'}</div>
                            </TableCell>
                            <TableCell className="text-right">
                              <CardEditMenu key={n.id} selectedData={n.id} 
                                onSelectRow={onSelectCustomer}/>
                            </TableCell>
                          </TableRow>
                        )
                        :
                        <tr>
                            <td className="text-danger" colSpan="9">
                                <IntlMessages id="table.noData"/>
                            </td>
                        </tr>
                      }
                    </TableBody>
                    <TableFooter>
                      <TableRow>
                        <TablePagination
                          count={Customers.length}
                          rowsPerPage={rowsPerPage}
                          page={page}
                          onChangePage={handleChangePage}
                          onChangeRowsPerPage={handleChangeRowsPerPage}
                        />
                      </TableRow>
                    </TableFooter>
                  </Table>
                </div>
              </div>
            ):(
              <div className="loader-view"
                      style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                  <CircularProgress/>
              </div>        
            )
          }

          <SweetAlert show={warning}
              warning
              confirmBtnText="Go Back"
              confirmBtnBsStyle="danger"
              cancelBtnBsStyle="default"
              title="Warning!"
              onConfirm={() => History.go(-1)}
          >                
          Data not cached, so you can not view this page.
          </SweetAlert>

          <NotificationContainer/>
        </div>
  );
};

export default CustomerTable;
